import { mapGetters, mapMutations } from 'vuex'

export default {
  data: () => ({
    checkPwaInstallInterval: null
  }),

  methods: {
    ...mapMutations({
      setPWA: 'app/SET_PWA',
      setPwaInstalling: 'app/SET_PWA_INSTALLING',
      setPrompt: 'app/SET_PROMPT'
    }),

    promptInstall () {
      console.log(this.promptPWA)
      if (this.promptPWA && 'prompt' in this.promptPWA) {
        console.log(this.promptPWA)
        // Show the prompt:
        console.log(1)
        this.setPwaInstalling(true)
        this.promptPWA.prompt()
        console.log(2)
        this.setPwaInstalling(true)
        // Wait for the user to respond to the prompt:
        this.promptPWA.userChoice.then(choiceResult => {
          if (choiceResult.outcome === 'accepted') {
            this.pwaInstallChoice = true
            console.log('pwaInstallChoice = ' + this.pwaInstallChoice)
            // this.setPWA(false)
            this.setPwaInstalling(true)
            this.listenPwaInstalled()
            console.log('User accepted the install prompt')
          } else {
            window.addEventListener(
              'beforeinstallprompt',
              this.beforeinstallpromptHandler.bind(this),
              { once: true }
            )
            console.log('User dismissed the install prompt')
          }
        })
        this.setPrompt(null)
      }
    },

    beforeinstallpromptHandler (event) {
      event.preventDefault()

      // Stash the event so it can be triggered later.
      this.setPrompt(event)

      this.setPWA(true)
      console.log(this.isPwa)
    },

    listenPwaInstalled () {
      if (this.checkPwaInstallInterval !== null) {
        return
      }

      if ('getInstalledRelatedApps' in window.navigator) {
        this.checkPwaInstallInterval = setInterval(async () => {
          const relatedApps = await navigator.getInstalledRelatedApps()
          console.log('relatedApps', relatedApps)
          for (const app of relatedApps) {
            console.log(app)
            if (app.platform === 'webapp') {
              this.setPwaInstalling(false)
              this.setPWA(false)
              clearInterval(this.checkPwaInstallInterval)
            }
          }
        }, 500)
      } else {
        console.log('"getInstalledRelatedApps" not supported!')
        this.setPwaInstalling(false)
        this.setPWA(false)
        clearInterval(this.checkPwaInstallInterval)
      }
    }
  },

  computed: {
    ...mapGetters({
      promptPWA: 'app/promptPWA',
      isPwa: 'app/isPwa'
    })
  },

  mounted () {
    window.addEventListener(
      'beforeinstallprompt',
      this.beforeinstallpromptHandler.bind(this),
      { once: true }
    )

    window.addEventListener('load', () => {
      if (navigator.standalone) {
        console.log('Launched: Installed (iOS)')
        this.setPWA(false)
      } else if (matchMedia('(display-mode: standalone)').matches) {
        this.setPWA(false)
        console.log('Launched: Installed')
      } else {
        if (this.promptPWA) {
          this.setPWA(true)
        } else {
          this.setPWA(false)
        }
        console.log('Launched: Browser Tab')
      }
      console.log(this.promptPWA)
    })

    window.addEventListener('appinstalled', event => {
      console.log('appinstalled')
      this.setPwaInstalling(true)
      this.listenPwaInstalled()
      this.setPWA(false)
    })
  },

  beforeDestroy () {
    clearInterval(this.checkPwaInstallInterval)
  }
}
