export const SET_SNACKBAR_MESSAGE = (state, message) => {
  state.snackbarMessage = message
}

export const SET_NOTIFICATION_PERMISSION = (state, payload) => {
  state.notificationPermission = payload
}

export const SET_PWA_INSTALLING = (state, status) => {
  state.isPwaInstalling = status
}

export const SET_PWA = (state, res) => {
  console.log('set pwa: ' + res)
  state.isPwa = res
}

export const SET_PROMPT = (state, event) => {
  state.promptPWA = event
}

export const SET_PLATFORM = (state, platform) => {
  state.platform = platform
}

export const SET_BROWSER = (state, browser) => {
  state.browser = browser
}

export const SET_SERVICE_WORKER_LOADED_STATUS = (state, status) => {
  console.log(status)
  state.serviceWorkerLoaded = status
}

export const SET_LANGUAGE = (state, lang) => {
  console.log(lang)
  state.language = lang
}
