<script>

import { mapGetters } from 'vuex'

export default {
  name: 'InstallPWA',

  data: () => ({
    shown: false,
    platformLocal: ''
  }),
  
  computed: {
    ...mapGetters({
      platform: 'app/platform',
      serviceWorkerLoaded: 'app/serviceWorkerLoaded',
      isPwa: 'app/isPwa',
      isPwaInstalling: 'app/isPwaInstalling'
    }),

    installButtonText () {
        console.log(this.serviceWorkerLoaded)
        if (!this.serviceWorkerLoaded) {
            return 'Compatibility check...'
        } else if (this.isPwa && this.serviceWorkerLoaded && ! this.isPwaInstalling) {
            return 'Install'
        } else if (this.isPwa && this.isPwaInstalling) {
            return 'Installing...'
        } else {
            return 'Open'
        }
    }
  },

  methods: {

    // whatPlatform(platformObject) {
    //     let platform
    //     if (platformObject.is.mobile && platformObject.is.ios) {
    //         platform = 'ios'
    //     } else if (platformObject.is.mobile) {
    //         platform = 'mobile'
    //     } else if (platformObject.within?.iframe) {
    //         platform = 'extension'
    //     } else {
    //         platform = 'desktop'
    //     }
    //     this.platformLocal = platform
    // },

    install () {
      this.$emit('install')
    },

    background(){
      document.body.classList.add('animateBackground')
    },
    toTop(){
      let fullBody = document.querySelector('.hiddenMain');
      fullBody.classList.add('newAnim');
    },
    headerMoving(){
      let head = document.querySelector('.headerFull');
      head.classList.add('headImg');
    },
    overFlow(){
      let overF = document.querySelector('.hiddenFlow');
      overF.classList.remove('hiddenFlow');
    },
    imgOpacAdd(){
      let head = document.querySelector('.headerFull');
      head.classList.add('headOpacity');
    },
    imgOpacRem(){
      let head = document.querySelector('.headerFull');
      head.classList.remove('headOpacity');
    }
  },

  mounted(){
    // this.whatPlatform(this.Platform)
    this.imgOpacAdd();
    setTimeout(this.imgOpacRem, 2000);
    setTimeout(this.background,2100);
    setTimeout(this.headerMoving, 2000);
    setTimeout(this.toTop,2000);
    setTimeout(this.overFlow,2000);
  }
}
</script>

