export const setPlatform = ({ commit }, platformObject) => {
  console.log(platformObject)
  let platform

  if (platformObject.is.mobile && platformObject.is.ios) {
    platform = 'ios'
  } else if (platformObject.is.mobile) {
    platform = 'mobile'
  // } else if (platformObject.within?.iframe) {
  //   platform = 'extension'
  } else {
    platform = 'desktop'
  }

  commit('SET_PLATFORM', platform)
  return platform
}

export const setBrowser = ({ commit }) => {
  const browser = detect()

  if (browser) {
    commit('SET_BROWSER', browser.name)
  }
}
